import React from 'react';
import PropTypes from 'prop-types';
import ModalAlert from './ModalAlert';

export default class ConfirmBox extends ModalAlert {
    static propTypes = {
        onConfirm: PropTypes.func.isRequired,
        ...ModalAlert.propTypes,
    };


    onConfirm() {
        this.props.onConfirm();
    }

    onCancel() {
        this.props.onClose();
    }

    open() {
        this.setState({visible: true});
        setTimeout(() => {
            if (this.refs.cancelButton) {
                this.refs.cancelButton.focus();
            }
            this.setState({active: true});
        }, 300);
    }

    renderFooter() {
        if (this.props.renderFooter) {
            return this.props.renderFooter();
        }
        return (
            <div>
                <button type="button" className="btn btn-danger" onClick={this.onConfirm.bind(this)}>Yes</button>
                <button type="button" ref="cancelButton" className="btn btn-primary" onClick={this.onCancel.bind(this)}>No</button>
            </div>
        )
    }
}



