import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import moment from 'moment';


export default class DatePicker extends Component {
    static propTypes = {
        onChange: PropTypes.func.isRequired,
        value: PropTypes.string,
        allowWeekends: PropTypes.bool,
        readOnly: PropTypes.bool,
        minDate: PropTypes.string,
    };
    constructor() {
        super();
        this.state = {
            selected: moment(),
            display: moment(),
            visible: false,
        }
    }

    componentWillMount() {
        // console.log('componentWillMount()', this.props);
        this.setState({
            selected: moment(this.props.value || moment()),
            display: moment(this.props.value || moment()),
        });
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            selected: moment(nextProps.value || moment()),
            display: moment(nextProps.value || moment()),
        });
    }


    onClickInput() {
        this.setState({
            visible: !this.state.visible
        });
    }

    onBlur(ev) {
        // console.log(ev);
        // if (this.state.visible) {
        //     setTimeout(() => {
        //         this.setState({
        //             visible: false
        //         });
        //     }, 50);
        // }
    }

    onClickPrevMonth() {
        this.setState({
            display: this.state.display.subtract({month: 1})
        });
    }

    onClickNextMonth() {
        this.setState({
            display: this.state.display.add({month: 1})
        });
    }

    onSelectDay(day) {
        if (this.props.readOnly) {
            day = this.state.selected;
        } else if (this.props.minDate && moment(this.props.minDate) > day) {
            return;
        } else if (!this.props.allowWeekends) {
            if (day.day() === 0 || day.day() === 6) {
                return;
            }
        }
        this.setState({
            selected: day,
            visible: false
        });
        if (!this.props.readOnly) {
            this.props.onChange(day.toISOString());
        }

    }

    calcWeeks() {
        let day = this.state.display;
        let startWeek = day.clone().date(1).day(-7).week();
        let endWeek = day.clone().date(1).add({months: 1}).subtract({days: 1}).week();
        if (endWeek < startWeek) {
            endWeek += day.clone().date(1).day(-7).weeksInYear()
        }
        return endWeek - startWeek;
    }

    renderDayNames() {
        let day = this.state.display.clone();
        day.date(1).day(0);
        let dayNames = [];
        for (let d = 0; d < 7; d += 1) {
            let rendered = (
                <div className="day-container" key={`${d}`}>
                    <div className="day day-name">{day.format('dd')}</div>
                </div>
            );
            dayNames.push(rendered);
            day.add(1, 'd');
        }
        return dayNames;
    }

    renderDayNumbers() {
        let today = moment();
        let day = this.state.display.clone();
        let weeks = this.calcWeeks();
        let minDate = moment(this.props.minDate || moment())

        day.date(1).day(0);
        let days = [];
        for (let w = 0; w < weeks; w += 1) {
            for (let d = 0; d < 7; d += 1) {
                let className = {
                    day: true,
                    'in-month': day.year() === this.state.display.year() && day.month() === this.state.display.month(),
                    today: day.format('YYYY-MM-DD') === today.format('YYYY-MM-DD')
                                && day.format('YYYY-MM-DD') !== this.state.selected.format('YYYY-MM-DD'),
                    selected: day.format('YYYY-MM-DD') === this.state.selected.format('YYYY-MM-DD'),
                    disabled: (this.props.readOnly
                        || (!this.props.allowWeekends && (day.day() === 0 || day.day() === 6))
                        || (this.props.minDate && day < minDate)
                    )
                };
                className[day.format('[day]-d')] = true;
                let rendered = (
                    <div className="day-container" key={`${w}-${d}`} onClick={this.onSelectDay.bind(this, day.clone())}>
                        <div className={classNames(className)}>{day.date()}</div>
                    </div>
                );
                days.push(rendered);
                day.add(1, 'd');
            }
        }
        return days;
    }

    renderDatePicker() {

        let popoverClassName = classNames({
            popover: true,
            fade: true,
            bottom: true,
            in: this.state.visible
        });
        const popoverStyle = {
            top: this.refs && this.refs.datePicker ? this.refs.datePicker.offsetHeight : '100px',
            display: this.props.title === '' ? 'none' : 'block'
        };

        let dayNames = this.renderDayNames();
        let days = this.renderDayNumbers();
        return (
            <div className={popoverClassName} style={popoverStyle}>
                <div className="arrow" style={{left: '25%'}} />
                <h3 className="popover-title" style={{display: this.props.title === '' ? 'none' : 'block'}}>
                    <div className="row">
                        <div className="month-selector" onClick={this.onClickPrevMonth.bind(this)}><span className="glyphicon glyphicon-arrow-left" /></div>
                        <div className="month-name">{this.state.display.format('MMM YYYY')}</div>
                        <div className="month-selector" onClick={this.onClickNextMonth.bind(this)}><span className="glyphicon glyphicon-arrow-right" /></div>
                    </div>
                    <div className="row">
                        {dayNames}
                    </div>
                </h3>
                <div className="popover-content">
                    <div className="row">
                        {days}
                    </div>
                </div>
            </div>
        );
    }

    render() {
        const datePicker = this.state.visible ? this.renderDatePicker() : null;
        const {className, onChange, value, format, title, allowWeekends, minDate, ...props} = this.props;
        return (
            <div style={{position: 'relative'}} className="date-picker">
                <input type="text" className={classNames("form-control", className)}  ref="datePicker"
                       value={this.state.selected.format('DD MMM YYYY')}
                       {...props}
                       onChange={() => {}}
                       onClick={this.onClickInput.bind(this)} />
                {datePicker}
            </div>
        )
    }
}




