import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

export default class InputGroupBtn extends Component {
    static propTypes = {
        btnClass: PropTypes.string.isRequired,
        btnIcon: PropTypes.string.isRequired,
        onChange: PropTypes.func.isRequired,
        onClickBtn: PropTypes.func.isRequired,
    };

    onChange(ev) {
        this.props.onChange(ev.target.value);
    }

    onClickBtn() {
        this.props.onClickBtn();
    }

    render() {
        let {className, style, btnClass, btnIcon, onClickBtn, onChange, ...otherProps} = this.props;
        return (
            <div className="input-group" style={style}>
                <input {...otherProps} className={classNames('form-control', className)}
                       onChange={this.onChange.bind(this)}/>
                <span className="input-group-btn">
                    <button className={classNames('btn', `btn-${btnClass}`)} type="button"
                            onClick={this.onClickBtn.bind(this)} >
                        <span className={classNames("glyphicon", `glyphicon-${btnIcon}`)} />
                    </button>
                </span>
            </div>
        )
    }
}



