import { observable, computed } from 'mobx';
import { fetchGET } from '../fetch';
import Extendable from './Extendable';
import Promise from 'bluebird';


export default class Warehouse extends Extendable {
    @observable WarehouseCode = '';
    @observable WarehouseDesc = '';
    @observable selected = false;

    constructor(params = {}) {
        super(params);
    }

    /**
     *
     * @param {string} company='chums' - chums|bc|CHI|BCS
     * @returns {Promise.<[Warehouse]>}
     */
    static loadCollection(company = 'chums') {
        const url = '/node/search/whse/:company/'.replace(':company', company);
        return new Promise((resolve, reject) => {
            fetchGET(url)
                .then(response => {
                    resolve(response.result.map(w => new Warehouse(w)));
                })
                .catch(err => {
                    reject(err);
                })
        })
    }
}


