import { observable, computed } from 'mobx';
import { fetchGET } from '../fetch';
import Extendable from './Extendable';
import Promise from 'bluebird';


export default class Category3 extends Extendable {
    @observable Category3 = '';
    @observable selected = false;

    constructor(params = {}) {
        super(params);
    }

    /**
     *
     * @param company
     * @return {Promise.<Category3[]>}
     */
    static loadCollection(company = 'chums') {
        const url = '/node/search/cat3/:company'.replace(':company', company);
        return new Promise((resolve, reject) => {
            fetchGET(url)
                .then(response => {
                    resolve(response.result.map(c => new Category3(c)));
                })
                .catch(err => {
                    console.log(err.message);
                    reject(err);
                });
        });
    }
}


