import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import keydown from 'react-keydown';

export default class ModalAlert extends Component {
    static propTypes = {
        active: PropTypes.bool,
        title: PropTypes.string,
        body: PropTypes.any,
        onClose: PropTypes.func.isRequired,
        small: PropTypes.bool,
        large: PropTypes.bool,
        renderFooter: PropTypes.func,
    };

    constructor() {
        super();
        this.state = {
            active: false,
            visible: false,
        };
    }

    componentWillMount() {
        this.setState({
            active: this.props.active,
            visible: this.props.active,
        });
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.active && !this.state.active) {
            this.open();
        } else if (!nextProps.active && this.state.active) {
            this.close();
        }
    }

    open() {
        this.setState({visible: true});
        setTimeout(() => {
            if (this.refs.okbutton) {
                this.refs.okbutton.focus();
            }
            this.setState({active: true});
        }, 300);
    }

    close() {
        this.setState({active: false});
        setTimeout(() => {
            this.setState({visible: false});
        }, 300);
    }

    @keydown('esc')
    onClickOutside() {
        this.props.onClose();
    }

    onClickInside(ev) {
        ev.preventDefault();
        ev.stopPropagation();
        // really, this does nothing but stop it from propagating to the parent objects
    }

    @keydown('enter')
    onClickOK(ev) {
        ev.stopPropagation();
        this.props.onClose();
    }

    renderFooter() {
        if (this.props.renderFooter) {
            return this.props.renderFooter();
        }
        return (
            <button ref="okbutton" className="btn btn-primary" onClick={this.onClickOK.bind(this)}>Ok</button>
        )
    }

    render() {
        let footer = this.renderFooter();
        let dialogClassNames = classNames({
            'modal-dialog': true,
            'modal-sm': this.props.small,
            'modal-lg': this.props.large,
        });

        const body = this.props.body && this.props.body.message ? this.props.body.message : null;
        return (
            <div className={classNames("modal fade", this.state.active ? 'in' : '')} role="dialog"
                 style={{display: this.state.visible ? 'block' : 'none', backgroundColor: 'rgba(0,0,0,0.5)'}}
                 onClick={this.onClickOutside.bind(this)}   >
                <div className={dialogClassNames} role="document" onClick={this.onClickInside.bind(this)}>
                    <div className="modal-content">
                        <div className="modal-header">
                            {this.props.title || 'Oh Snap!'}
                        </div>
                        <div className="modal-body">
                            {this.props.children || body || this.props.body || 'Hey! Something happened'}
                        </div>
                        <div className="modal-footer">
                            {footer}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


