import { observable, toJS, action } from 'mobx';
import { fetchGET, fetchPOST, Warehouse, ProductLine, Category2, Category3, Category4 } from 'chums-components';
import Item from './types/Item';
import ItemStatus from './types/ItemStatus';


class ProductStatusStore {
    /**
     *
     * @type {Item}
     */
    @observable itemParams = new Item({Company: 'CHI'});
    @observable company = 'chums';
    @observable warehouses = [];
    @observable productLines = [];
    @observable category2 = [];
    @observable category3 = [];
    @observable category4 = [];
    /**
     *
     * @type {ItemStatus}
     */
    @observable itemStatus = {};

    @observable items = [];
    @observable hide0 = true;
    @observable loadingOptions = 0;
    @observable isAdmin = false;

    constructor() {
        fetchGET('/node/user/validate/role/inventory_admin')
            .then(response => {
                this.isAdmin = response.success;
                return null;
            })
            .catch(err => {
                this.isAdmin = false;
            });
    }

    loadWarehouses(company = 'chums') {
        Warehouse.loadCollection(company)
            .then(response => {
                this.warehouses = response;
                this.loadingOptions += 1;
            })
            .catch(err => {
                console.log(err.message);
            });
    }

    loadProductLines(company = 'chums') {
        ProductLine.loadCollection(company)
            .then(response => {
                this.productLines = response;
                this.loadingOptions += 1;
            })
            .catch(err => {
                console.log(err.message);
            });
    }

    loadCategory2(company = 'chums') {
        Category2.loadCollection(company)
            .then(response => {
                this.category2 = response;
                this.loadingOptions += 1;
            })
            .catch(err => {
                console.log(err.message);
            });
    }

    loadCategory3(company = 'chums') {
        Category3.loadCollection(company)
            .then(response => {
                this.category3 = response;
                this.loadingOptions += 1;
            })
            .catch(err => {
                console.log(err.message);
            });
    }

    loadCategory4(company = 'chums') {
        Category4.loadCollection(company)
            .then(response => {
                this.category4 = response;
                this.loadingOptions += 1;
            })
            .catch(err => {
                console.log(err.message);
            });
    }
    
    loadItemStatus() {
        ItemStatus.getCollection()
            .then(response => {
                response.map(is => {
                    this.itemStatus[is.code] = is;
                    this.loadingOptions += 1;
                })
            })
            .catch(err => {
                console.log(err.message);
            });
    }

    loadItems() {
        return Item.loadCollection(store.itemParams)
            .then(result => {
                this.items = result;
                console.log(this.items.length);
                Object.keys(this.itemStatus)
                    .map(k => {
                        this.itemStatus[k].resetTotal();
                    });
                this.items.forEach(i => {
                    this.itemStatus[i.ItemStatus].total.add(i);
                });
                console.log(this.items.length);
            })
            .catch(err => {
                reject(err);
            });
    }


    onChangeCompany(company) {
        this.loadingOptions = 0;
        this.itemParams.Company = company;
        this.loadWarehouses(company);
        this.loadProductLines(company);
        this.loadCategory2(company);
        this.loadCategory3(company);
        this.loadCategory4(company);
        this.loadItemStatus();
    }
}

export const store = new ProductStatusStore();


