import { observable, computed } from 'mobx';
import { fetchGET, fetchPOST, Extendable } from 'chums-components/src/index';
import Promise from 'bluebird';
import Item from './Item';

export default class ItemStatus extends Extendable {
    @observable id = 0;
    @observable code = '';
    @observable description = '';
    @observable total = new Item();
    @observable selected = false;

    constructor(params = {}) {
        super(params);
        this.resetTotal();
    }

    resetTotal() {
        this.total = new Item({ItemCode: this.code, ItemCodeDesc: this.description, ItemStatus: this.code});
    }

    /**
     *
     * @param {Item} item
     */
    addItem(item) {
        this.total.add(item);
    }

    static getCollection() {
        const url = '/node-dev/production/item/status';
        return new Promise((resolve, reject) => {
            fetchGET(url)
                .then(response => {
                    resolve(response.result.map(is => new ItemStatus(is)));
                })
                .catch(err => {
                    reject(err);
                });
        });
    }
}


