import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DropDownInput from './DropDownInput';
import { fetchGET } from './fetch';

export default class CustomerDropDown extends Component {
    minLength = 2;

    static propTypes = {
        company: PropTypes.string.isRequired,
        onSelect: PropTypes.func.isRequired,
    };

    constructor() {
        super();
        this.state = {
            customerLookup: '',
            lookupResult: [],
            timer: null,
            loading: false
        };
    }

    setAccountLookup(val) {
        if (val.target) {
            val = val.target.value;
        }
        if (val === '') {
            clearTimeout(this.timer);
            this.setState({
                lookupResult: [],
                customerLookup: '',
                timer: null,
                loading: false,
            });
            return;
        }
        this.setState({customerLookup: val});
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }
        const timer = setTimeout(this.doLookup.bind(this), 350);
        this.setState({timer: timer});
    }

    doLookup() {
        if (this.state.customerLookup.length < this.minLength) {
            return;
        }
        const url = `/node-dev/search/customer/:company/:search`
            .replace(':company', encodeURIComponent(this.props.company || 'chums'))
            .replace(':search', encodeURIComponent(this.state.customerLookup));
        fetchGET(url)
            .then(res => {
                this.setState({lookupResult: res.result, loading: false});
            });
        this.setState({loading: true});
    }

    static formatter(acct) {
        return (
            `${acct.ARDivisionNo}-${acct.CustomerNo} ${acct.CustomerName}`
        );
    }

    onSelectAccount(acct) {
        this.setState({
            customerLookup: acct.CustomerName
        }, () => {
            this.props.onSelect(acct);
        });
    }


    render() {
        return (
            <DropDownInput placeholder="search" minLength={this.minLength}
                           value={this.state.customerLookup}
                           items={this.state.lookupResult}
                           loading={this.state.loading}
                           formatter={CustomerDropDown.formatter}
                           className="form-control"
                           onSelect={this.onSelectAccount.bind(this)}
                           onChange={this.setAccountLookup.bind(this)}
            />
        )
    }
}



