import { observable, computed } from 'mobx';
import { fetchGET } from '../fetch';
import Extendable from './Extendable';
import Promise from 'bluebird';


export default class ProductLine extends Extendable {
    @observable ProductLine = '';
    @observable ProductLineDesc = '';
    @observable ProductType = '';
    @observable Valuation = '';
    @observable ExplodeKitItems = '';

    constructor(params = {}) {
        super(params);
    }

    /**
     *
     * @param company
     * @return {Promise.<ProductLine[]>}
     */
    static loadCollection(company = 'chums') {
        const url = '/node/search/prodline/:company'.replace(':company', company);
        return new Promise((resolve, reject) => {
            fetchGET(url)
                .then(response => {
                    resolve(response.result.map(pl => new ProductLine(pl)));
                })
                .catch(err => {
                    console.log(err.message);
                    reject(err);
                });
        });
    }
}


