'use strict';

export default class Extendable {
    constructor(obj) {
        this.parse(obj)
    }

    parse(obj) {
        Object.keys(obj).map(key => {
            if (key in this) {
                try {
                    this[key] = obj[key];
                } catch(ex) {
                    // console.log('Extendable()', key, ex);
                }
            }
        });
    }
}


